body {
    background-image: url('https://i.blogs.es/e239cb/crear-wallpapers-portada/1366_2000.jpeg');
    background-size: cover;
    background-position: center;
    height: 100vh;
    margin: 0;
    padding: 0;
  }
  
  .container {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: rgba(255, 255, 255, 0.8); /* Fondo semitransparente */
    backdrop-filter: blur(5px); /* Aplica un desenfoque al fondo del contenedor */
  }
  
  /* Estilos para los cuadros */
  .box {
    background-image: url('https://i.blogs.es/e239cb/crear-wallpapers-portada/1366_2000.jpeg'); /* Imagen de fondo */
    background-size: cover;
    background-position: center;
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 20px;
    margin-bottom: 20px;
    filter: blur(5px); /* Aplica un desenfoque a la imagen de fondo */
  }
  
  
  h2 {
    text-align: center;
  }
  
  label {
    display: block;
    margin-bottom: 10px;
  }
  
  input[type="number"],
  select {
    width: 100%;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-sizing: border-box;
    margin-top: 5px;
  }
  
  button {
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    padding: 10px 20px;
    cursor: pointer;
    font-size: 16px;
  }
  
  button:hover {
    background-color: #0056b3;
  }
  .button-container {
    text-align: center;
  }
  
  
  .resultados h3 {
    margin-bottom: 10px;
  }
  
  .resultados p {
    margin: 5px 0;
  }
  .resultados {
    margin: 0 auto;
    width: fit-content;
    margin-top: 20px;

  }
      